import React from 'react'
import classnames from 'classnames'
import TouchFeedback from 'rmc-feedback'
import Icon from '../icon'
import { SpinLoading } from 'antd-mobile'
interface ButtonProps {
  size?: 'large' | 'small'
  mode?: 'default' | 'primary' | 'warning' | 'ghost' | 'text'
  onClick?: (event: React.MouseEvent<HTMLElement, MouseEvent>) => void
  visible?: boolean
  disabled?: boolean
  loading?: boolean
  rounded?: string
  icon?: React.ReactNode | string
  iconPosition?: 'top' | 'right' | 'bottom' | 'left'
  className?: string
  style?: React.CSSProperties
}
export default class Button extends React.Component<ButtonProps> {
  static defaultProps = {
    size: 'large',
    mode: 'primary',
    visible: true,
    disabled: false,
    iconPosition: 'left'
  }

  onClick = e => {
    const { onClick, disabled } = this.props
    if (disabled) return
    onClick?.(e)
  }

  render () {
    const { children, size, mode, visible, disabled, loading, icon, iconPosition, className, style, rounded = 'base' } = this.props
    if (!visible) return null
    const wrapCls = classnames('arc-button', className, `arc-button-${size}`, {
      'arc-button-default': mode === 'default' || !mode,
      'arc-button-primary': mode === 'primary',
      'arc-button-warning': mode === 'warning',
      'arc-button-ghost': mode === 'ghost',
      'arc-button-text': mode === 'text',
      'arc-button-disabled': disabled,
      [`arc-button-${size}-column`]: iconPosition === 'top' || iconPosition === 'bottom',
    }, `rounded-${rounded}`)

    let iconEl = icon
    if (loading) {
      iconEl = <SpinLoading className='arc-button-icon' style={{ '--size': size === 'small' ? '0.3rem' : '0.44rem' }} color='white' />
    } else if (typeof icon === 'string') {
      iconEl = <div className='arc-button-icon'><Icon type={icon} size={size === 'small' ? 'xxs' : 'md'} /></div>
    } else if (icon) {
      iconEl = <div className='arc-button-icon'>{iconEl}</div>
    }
    const topPosition = <>
      {iconEl}
      {children}
    </>
    const rightPosition = <>
      {children}{iconEl}
    </>
    const bottomPosition = <>
      {children}
      {iconEl}
    </>
    const leftPosition = <>
      {iconEl}{children}
    </>
    return (
      <TouchFeedback activeClassName='arc-button-active' disabled={disabled}>
        <a className={wrapCls} style={style} onClick={this.onClick}>
          {iconPosition === 'top' && topPosition}
          {iconPosition === 'right' && rightPosition}
          {iconPosition === 'bottom' && bottomPosition}
          {iconPosition === 'left' && leftPosition}
        </a>
      </TouchFeedback>
    )
  }
}
